.row {
    display: flex; }

.body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #C4A8A8FF; }

.box {
    padding: 1rem;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 0.25rem;

    &-primary {
        background-color: #007bff;
        color: #fff;

        &:hover, &:focus {
            background-color: #0069d9;
            border-color: #0062cc; } }

    &-success {
        background-color: #28a745;
        color: #fff;

        &:hover, &:focus {
            background-color: #218838;
            border-color: #1e7e34; } }

    &-warning {
        background-color: #ffc107;
        color: #212529;

        &:hover, &:focus {
            background-color: #e0a800;
            border-color: #d39e00; } }

    &-danger {
        background-color: #dc3545;
        color: #fff;

        &:hover, &:focus {
            background-color: #c82333;
            border-color: #bd2130; } } }


.progress-section {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999; }


.progress {
    color: white;
    text-align: center;
    width: 30%;
    height: 60%; }

.loader {
    width: 30%;
    height: 30%; }

.loader, .progress {
    background-color: #343030;
    position: absolute;
    border: 42px solid #705050;
    border-top: 14px solid #c4a8a8;
    border-radius: 50%;
    top: 20vh;
    left: 20vw;
    animation: spin 2s ease-in-out infinite;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; }

.loader:before, .progress:before {
    position: absolute;
    left: 0;
    color: #705050;
    width: 50vh;
    text-align: center;
    transform: rotate(-90deg) /* Ã„ndern Sie den Rotationswinkel der Schrift auf -90 Grad, um ihn im Uhrzeigersinn zu drehen */;
    animation: spin 2s ease-in-out infinite reverse; }

.loader:before {
    content: "Bitte Karte auflegen";
    font-size: 5rem; }

.progress:before {
    content: "Reset ID";
    font-size: 5rem; }

/* Button-Styling */
.edelstahl-button {
    background: linear-gradient(to bottom, #f4f4f4, #e4e4e4);
    width: 10vh;
    height: 10vh;
    border-radius: 50%;
    border: none;
    box-shadow: 0 2px 0 #7f8c8d, inset 0 0 0 2px #bdc3c7, inset 0 0 5px rgba(255, 255, 255, 0.6);
    color: #555;
    cursor: pointer;
    font-size: 16px;
    padding: 10px 20px;
    text-shadow: 0 3px 0 rgba(255, 255, 255, 0.8);

    &:active {
        box-shadow: 0 1px 0 #7f8c8d, inset 0 0 0 2px #bdc3c7, inset 0 0 3px rgba(255, 255, 255, 0.4);
        top: 1px; } }




@keyframes spin {
    from {
        transform: rotate(0deg); }
    to {
        transform: rotate(-360deg); } }
